@import "../less/colors.less";
@import "../less/fonts.less";
@import "../less/variables.less";

.container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-top: 4rem;
  margin-bottom: 2rem;
}

.description {
  width: 50%;
  margin-top: 3rem;

  text-align: center;
  font-size: 1.75rem;
  font-weight: 300;
}

.imageContainer {
  position: relative;
}

.permanentMarker {
  position: absolute;
  font-family: @fontPermanentMarker;
  color: @secondaryColor;
  font-size: 1.25rem;

  img {
    position: absolute;
  }
}

.advokaten {
  left: -4rem;
  top: -1.75rem;
  transform: rotate(-15deg);

  img {
    transform: rotate(25deg);
    top: 2rem;
    left: 1rem;
  }
}

.psykologen {
  right: -4rem;
  bottom: -1.75rem;
  transform: rotate(-15deg);

  img {
    transform: rotate(225deg);
    top: -2.5rem;
    left: 5.5rem;
  }
}
